import React, { Component } from 'react';
import Footer from './Footer';
import Abc from './Header';//here we can use any word in place of Header but first letter should be capital
import JSON from './db.json' ;
import NewsDisplay from './NewsDisplay'

class Home extends Component{
    constructor(){
        super()
        this.state={
            news:JSON,
            filtered:JSON
        }

    }
    //we need what user is typing from header
    //header=>home
    /* var a=[2,4,6,8]
    a.filter((data))=>{return data>3}*/

    filterNews=(userInput)=>{
       const out=this.state.news.filter((data)=>{
           return data.name.indexOf(userInput)>-1
       })
       this.setState({filtered:out})
    }
    render(){
//console.log(this.state.news)
        return(
            <div>
                <Abc userData={(data)=>{this.filterNews(data)}}/>
            <NewsDisplay newsdata={this.state.filtered}/>
            <Footer year='2021'month='march'/></div>//Abc and Footer is the child components
            
        )
    }
    
}
export default Home