import React from 'react';

const NewsDisplay=(props)=>{
    console.log(props)
    const renderNews=props.newsdata.map((data)=>{
        return(
            <div>
                <h2>{data.name}</h2>
                <p>{data.age}</p>
            </div>
        )

    })
return(
        <div>
            {renderNews}
        </div>
    )
}
export default NewsDisplay